import React from 'react';
import { View, Text, ScrollView, TouchableOpacity } from 'react-native';
import { vh, Scale } from 'russel-responsive2';
import { useNavigation } from '@react-navigation/native';

export default function Widget(props) {
    const data = ['Overview', 'Installation', 'Usage']
    const data2 = ['Scale', 'Fluid', 'Viewport', 'Hover']
    const navigation = useNavigation();
    return (
        <View style={{ flex: 1, height: vh(100), flexDirection: Scale('column', 'column', 'row'), backgroundColor: '#171430' }}>
            <View style={{ height: 60, backgroundColor: '#222044', display: Scale('flex', 'flex', 'none'), justifyContent: 'center' }}>
                <Text style={{ fontSize: 24, fontWeight: 'bold', color: 'white', marginLeft: 20 }}>Russel</Text>
            </View>
            <View style={{ width: 250, display: Scale('none', 'none', 'flex') }}>
                <View style={{
                    marginHorizontal: 20, marginVertical: 20, borderRadius: 20,
                    backgroundColor: '#222044', flex: 1,
                }}>
                    <View style={{ height: 100, justifyContent: 'center', alignItems: 'center' }}>
                        <Text style={{ fontSize: 35, fontWeight: 'bold', color: 'white' }}>Russel</Text>
                    </View>
                    {data.map((item, index) => {
                        return (
                            <TouchableOpacity
                                onPress={() => navigation.navigate(item)}
                                key={index}
                            >
                                <Text style={{
                                    marginLeft: 20, fontSize: 18, color: 'white',
                                    marginTop: 20, fontWeight: '600'
                                }}>{item}</Text>
                            </TouchableOpacity>
                        )
                    })}
                    {data2.map((item, index) => {
                        return (
                            <TouchableOpacity onPress={() => navigation.navigate(item)}>
                                <Text key={index} style={{
                                    marginLeft: 40, fontSize: 15, color: 'white',
                                    marginTop: 20,
                                }}>{item}</Text>
                            </TouchableOpacity>
                        )
                    })}
                    <View style={{ height: 150, justifyContent: 'flex-end', alignItems: 'center', flex: 1 }}>
                        <TouchableOpacity>
                            <Text style={{
                                fontSize: 16, color: 'white', backgroundColor: '#5c6fe8', borderRadius: 12,
                                marginBottom: 15, paddingLeft: 25, paddingRight: 25, paddingBottom: 10, paddingTop: 10
                            }}>Donate For Me ❤️</Text>
                        </TouchableOpacity>
                    </View>
                </View>
            </View>
            <ScrollView style={{ flex: 4.5, height: vh(100) }} overScrollMode='never' showsVerticalScrollIndicator={false}>
                {props.children}
            </ScrollView>
        </View>
    );
}
