import React from 'react';
import { NavigationContainer } from '@react-navigation/native';
import { createNativeStackNavigator } from '@react-navigation/native-stack';
import Overview from './Overview';
import Installation from './Installation';
import Fluid from './Fluid';
import Scale from './Scale';
import Viewport from './Viewport';
import Hover from './Hover';

const Stack = createNativeStackNavigator();

export default function Navigation() {
    return (
        <NavigationContainer>
            <Stack.Navigator screenOptions={{
                headerShown: false, title: 'Russel'
            }}>
                <Stack.Screen name="Overview" component={Overview} />
                <Stack.Screen name="Installation" component={Installation} />
                <Stack.Screen name="Fluid" component={Fluid} />
                <Stack.Screen name="Scale" component={Scale} />
                <Stack.Screen name="Viewport" component={Viewport} />
                <Stack.Screen name="Hover" component={Hover} />
            </Stack.Navigator>
        </NavigationContainer>
    );
}
