import react from "react"
import { View, Text, TouchableOpacity } from "react-native"
import { Fluid, vw } from "russel-responsive2"
import * as Clipboard from 'expo-clipboard';

export function Judul() {
    const style = { fontSize: Fluid({ min: 25, max: 35 }), fontWeight: 'bold', color: 'white', marginTop: 20, marginLeft: 20 }
    return style
}

export function Deskripsi() {
    const style = { fontSize: 15, marginTop: 20, marginHorizontal: 20, color: 'white' }
    return style
}

export function Salin(props) {

    const copyToClipboard = async () => {
        await Clipboard.setStringAsync(props.title);

    };
    return (
        <View>
            <Text style={{ marginTop: 20, marginHorizontal: 20 }}>
                <Text style={{
                    fontSize: 15, color: 'white', flexDirection: 'row',
                    backgroundColor: 'rgba(92, 111, 232, 0.15)', borderRadius: 15, justifyContent: 'space-between',
                    paddingBottom: 15, paddingTop: 15, paddingLeft: 15, paddingRight: 2
                }}>{props.title}
                    <TouchableOpacity onPress={copyToClipboard}>
                        <Text style={{
                            padding: 10, backgroundColor: '#5c6fe8', borderRadius: 10,
                            marginRight: 5, color: 'white', marginLeft: Fluid({ min: 20, max: 60 })
                        }}>
                            Copy
                        </Text>
                    </TouchableOpacity>
                </Text>
            </Text>
        </View>
    )
}